import React from 'react';
import PropTypes from 'prop-types';

import ContentSection from '../content-section';

import styles from './GoalsSection.module.css';

export const GoalsSection = ({ goals, ...remainingProps }) => {
    const renderGoals = () => goals.map((goal, index) => (
        <li key={ `goal-${index}` }>
            { goal }
        </li>
    ));

    return (
        <ContentSection { ...remainingProps }>
            <ul className={ styles.list }>
                { renderGoals() }
            </ul>
        </ContentSection>
    );
};

GoalsSection.propTypes = {
    goals: PropTypes.arrayOf(PropTypes.string).isRequired,
};

export default GoalsSection;
