import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { useReady } from '../../../hooks';
import {
    TitleAnimation,
    ScaleAndFadeAnimation,
} from '../../../components';
import Carousel from './carousel';

import styles from './HeroSection.module.css';

export const HeroSection = ({
    title,
    photos,
    className,
    blocksData,
}) => {
    const isReady = useReady();
    const sectionClasses = classNames(styles.hero, className);

    const renderBlocks = () => blocksData.blocks.map((block, index) => (
        <p key={ `block-${index}` }>{ block }</p>
    ));

    return (
        <section className={ sectionClasses }>
            <div className={ styles.content }>
                <TitleAnimation
                    className={ styles.title }
                    startAnimation={ isReady }>
                    <h2>{ title }</h2>
                </TitleAnimation>
                <ScaleAndFadeAnimation
                    startAnimation={ isReady }
                    className={ styles.introWrapper }>
                    <div className={ styles.intro }>
                        <h3 className={ styles.introTitle }>
                            { blocksData.title }
                        </h3>
                        <div className={ styles.blocks }>
                            { renderBlocks() }
                        </div>
                    </div>
                </ScaleAndFadeAnimation>
            </div>
            <ScaleAndFadeAnimation startAnimation={ isReady }>
                <Carousel
                    photos={ photos }
                    className={ styles.carousel } />
            </ScaleAndFadeAnimation>
        </section>
    );
};

HeroSection.propTypes = {
    title: PropTypes.string.isRequired,
    blocksData: PropTypes.shape({
        title: PropTypes.string.isRequired,
        blocks: PropTypes.arrayOf(PropTypes.string).isRequired,
    }).isRequired,
    photos: PropTypes.arrayOf(PropTypes.shape({
        url: PropTypes.string.isRequired,
        description: PropTypes.string.isRequired,
    })).isRequired,
    className: PropTypes.string,
};

export default HeroSection;
